@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  position: fixed;
  right: 0;
  left: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@font-face {
  font-family: 'Polya';
  src: url(/static/media/POLYA.b13aaec4.otf);
  font-weight: 700;
  font-style: normal;
}

.App-header {
  height: 100vh;
  font-size: 9vh;
  font-family: 'Polya';
  color: #D30100;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 5vh;
  margin-right: 5vw;
  margin-left: 5vw;
}

.App-stem {
  font-family: 'Roboto';
  font-size: 50%;
}

.App-stem a {
  color: #D30100;
  text-decoration: none;
  animation: glow 1s ease-in-out infinite alternate;
  transition:all 0.5s ease;
}

.App-stem a:hover {
  color: white;
  transition:all 0.5s ease;
}

@keyframes glow {
  from {
    text-shadow: 0 0 5px rgba(211,1,0, 0.25), 0 0 10px rgba(211,1,0, 0.25), 0 0 15px rgba(211, 211, 211, 0.25), 0 0 20px rgba(211, 211, 211, 0.25), 0 0 25px rgba(211, 211, 211, 0.25), 0 0 30px rgba(211, 211, 211, 0.25), 0 0 35px rgba(211, 211, 211, 0.25);
  }
  to {
    text-shadow: 0 0 10px rgba(211,1,0, 0.25), 0 0 15px rgba(156, 156, 252, 0.25), 0 0 20px rgba(156, 156, 156, 0.25), 0 0 25px rgba(156, 156, 156, 0.25), 0 0 30px rgba(156, 156, 156, 0.25), 0 0 35px rgba(156, 156, 156, 0.25), 0 0 40px rgba(156, 156, 156, 0.25);
  }
}

.App-countdown {
  max-width: 80vw;
  position: absolute; 
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  white-space: normal;
  white-space: initial;
  word-wrap: break-word;
}

#backgroundVideo {
  position: fixed;
  right: 0;
  left: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

.App-link {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 5.5vh;
  width: 5.7vh;
  margin-left: 3.125vw;
  margin-right: 3.125vw;
  position: relative;
}

.App-link img {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

.App-links {
  position: absolute;
  bottom: 10vh;
  
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

